.card {
  background: #fff;
  border-radius: 2px;
  position: relative;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
  height:80%;
  width:350px;
}

.card > .img-container {
  overflow: hidden;
  text-align: center;
  background:black;
}
.card > .img-container > img {
  height: 100%;
  width: 100%;
}



